import React from 'react';
import ReactDOM from 'react-dom';
import './App.css';
import  Card from './App.js';
import Data from './Data';

//fat arrow function
//const ncard =(a,b)=>{
//	return a+b;
//}
function ncard(val)
{
	return (
		<Card 
			imgsrc={val.imgsrc}
			title={val.title}
			sname={val.sname}
			link={val.link}
			/>
	);
}
ReactDOM.render(
	<>
		<h1 className="nav">TOP 5 NETFLIX SERIES</h1>
		<div className="main">
		{Data.map(ncard)}
		</div>
	</>,
	document.getElementById("root")
);
